.search-form {
  height: auto;
  background-color: #76b8e9;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
  display: grid;
}

.search-form input[type="text"] {
  width: 40%;
  height: 20px;
  border: 4mm ridge #f6f6f6;
  writing-mode: horizontal-tb;
  border-bottom-color: #b2b8db;
  border-radius: 30px;
  padding: 10px;
}

.search-form button {
  color: #8a8686;
  cursor: pointer;
  background-color: #f5f5f5;
  border-color: #fff;
  border-radius: 20px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.photo-card {
  cursor: crosshair;
  border: thick double #bde4fe;
  border-radius: 30px;
  padding: 10px;
  transition: transform .3s;
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #b0adad;
  border-color: #fff;
  border-radius: 5px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 50%;
  padding: 10px 20px;
  font-size: 16px;
}

